___CSS_LOADER_EXPORT___.push([module.id, `.tech-stack-cont {
  max-width: 100% !important;
  height: fit-content;
  position: relative;
  background-color: #f1f2f4;
}
.tech-stack-cont .tech-stack {
  width: 90vw;
  height: fit-content;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2vw;
  font-family: "Lexend", sans-serif;
  user-select: none;
}
.tech-stack-cont .tech-stack code {
  color: #d90c5a;
}
.tech-stack-cont .tech-stack h1 {
  font-size: 2vw;
}
.tech-stack-cont .tech-stack #typed-stack-header {
  margin-left: 3vw;
}
.tech-stack-cont .tech-stack .tech-stack-sec {
  height: fit-content;
  width: 80vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 2vw 5vw;
}
.tech-stack-cont .tech-stack .tech-stack-sec h1 {
  font-size: 3vw;
  font-weight: 600;
}
.tech-stack-cont .tech-stack .tech-stack-sec .tech-sec-title {
  font-size: 2vw;
  font-weight: 400;
  width: fit-content;
  padding: 1vw;
  background-color: #20252c;
  color: white;
  border-radius: 0.5vw;
}
.tech-stack-cont .tech-stack .tech-stack-sec .tech-row {
  align-items: baseline;
  font-size: 1.25vw;
}
.tech-stack-cont .tech-stack .tech-stack-sec h3 {
  width: fit-content;
  padding: 1vw;
  background-color: #20252c;
  color: #E9AE00;
  border-radius: 0.5vw;
}
.tech-stack-cont .tech-stack .tech-stack-sec h3:hover {
  background-color: #E9AE00;
  color: #20252c;
  box-shadow: 0 0 1vw #E9AE00;
}
.tech-stack-cont .tech-stack .tech-stack-sec li {
  margin: 2vw;
  width: max-content;
  padding: 1vw;
  background-color: #E9AE00;
  color: #20252c;
  border-radius: 0.5vw;
  text-decoration: none;
}
.tech-stack-cont .tech-stack .tech-stack-sec li:hover {
  background-color: #20252c;
  color: #E9AE00;
  box-shadow: 0 0 1vw #20252c;
