  height: 50vw;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.diary-preview-img-container .diary-overlay .diary-overlay-content {
  position: absolute;
  top: 30%;
  left: 5%;
  width: 90%;
  height: 2vw;
  user-select: none;
}
