  width: 80vw;
  max-width: 80vw;
  max-height: 53vw;
  margin: 4vw 5vw;
}
.diary-card .MuiCardHeader-avatar {
  width: 3vw;
  margin-right: 3vw;
}
.diary-card .MuiAvatar-root {
  width: 5vw;
  height: 5vw;
  font-size: 2vw;
}
.diary-card .MuiCardHeader-content {
  width: 18vw;
}
.diary-card .MuiCardHeader-title {
  font-size: 1.5vw !important;
  font-weight: 400 !important;
  width: inherit;
}
.diary-card .MuiCardHeader-subheader {
  font-size: 1.2vw !important;
  width: inherit;
}
.diary-card .MuiCardHeader-action {
  margin: 0;
  width: 3vw;
  height: 5vw;
}
.diary-card .MuiCardHeader-action button {
  width: inherit !important;
}
.diary-card .MuiCardHeader-action button svg {
  font-size: 3vw !important;
  position: relative;
  top: -1vw;
  width: 2.7vw;
  height: 2.7vw;
}
.diary-card .MuiCardContent-root {
  padding: 2vw;
  height: 20vw;
  width: 29vw;
  top: -1.5vw;
  position: relative;
}
.diary-card .MuiCardContent-root .MuiTypography-root {
  font-size: 1.5vw;
}
.diary-card .MuiCardActions-root {
  padding: 0;
  position: relative;
  top: 1vw;
  align-items: center;
  justify-content: space-between;
}
.diary-card .MuiCardActions-root button {
  font-size: 1vw;
  width: 2.5vw;
}

.card-header {
  height: 5vw !important;
  padding: 1.2vw !important;
}

.card-img {
  height: 25vw !important;
  width: 45vw !important;
  margin-left: 1.2vw !important;
}

.card-stack {
  margin-bottom: 2vw;
}

.flip180 {
  transform: rotateY(180deg);
}

.edit-pencil {
  width: 6vw;
  height: 6vw;
  position: relative;
  left: 81vw;
  top: 8vw;
  cursor: pointer;
}

.edit-pencil:active {
  transform: translateY(4px);
