  left: 60vw;
}
.landing #intro-strips .strip:nth-child(4) .strip-ylw {
  animation: grow 0.8s 6s forwards ease-in, shrink 0.4s 11.4s forwards ease-out;
}
.landing #intro-strips .strip:nth-child(4) .strip-noc {
