.landing #intro-strips .strip:nth-child(5) {
  left: 80vw;
}
.landing #intro-strips .strip:nth-child(5) .strip-ylw {
  animation: grow 0.7s 6s forwards ease-in, shrink 0.35s 11.35s forwards ease-out;
