  font-family: "Monoton", sans-serif;
  font-size: 5vw;
  animation: enlarge 2s 7s forwards ease-in-out;
}
.landing .title-intro.tag {
  font-family: "Rubik Doodle Shadow", system-ui;
  font-size: 5vw;
  animation: fadeInOut 2.5s 9s forwards ease-in-out;
}

@keyframes grow {
  from {
    height: 0vh;
  }
  to {
    height: 100vh;
  }
}
@keyframes shrink {
  from {
    height: 100vh;
  }
  to {
    height: 0vh;
