  width: 100%;
  height: 50vw;
  position: absolute;
  top: 0vw;
  left: 0;
  object-fit: cover;
  z-index: -1;
}
.travel-preview-img-container .travel-overlay {
  position: absolute;
  top: 0vw;
  left: 0vw;
  width: 100%;
  height: 50vw;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.travel-preview-img-container .travel-overlay .travel-overlay-content {
  position: absolute;
  top: 30%;
  left: 5%;
  width: 90%;
  height: 2vw;
  user-select: none;
}
.travel-preview-img-container .travel-overlay .travel-overlay-content .travel-overlay-text {
  position: unset;
  color: white;
  font-size: 2vw;
  font-weight: bold;
  text-align: center;
  font-family: "LXGW WenKai TC", cursive;
  font-style: normal;
}
